.menu-toggle {
    position: absolute;
    /* background-color: red; */
    width: 48px;
    height: 48px;
    left: calc(5vw - 12px);
    top: calc(5vh - 12px);
    cursor: pointer;
}

.menu-icon {
    position: absolute;
    left: 12px;
    top: 12px;
}

.menu-line {
    background-color: #6067f1;
    width: 24px;
    height: 3px;
    top: 8px;
    left: 0px;
    border-radius: 3px;
    position: absolute;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}
.menu-toggle:hover .menu-line {
    background-color: #e74c3c;
}

.menu-line.top {
    -webkit-transform: translate3d(0, -8px, 0);
            transform: translate3d(0, -8px, 0);
}

.menu-line.mid {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.menu-line.end {
    -webkit-transform: translate3d(0, 8px, 0);
            transform: translate3d(0, 8px, 0);
}

.menu-toggle.light .menu-line {
    background-color: #fff;
}

.menu-toggle.light:hover .menu-line {
    background-color: #bdc3c7;
}

.menu-toggle.open .menu-line.top {
    -webkit-transform: rotate(45deg) translate3d(0, 0px, 0);
            transform: rotate(45deg) translate3d(0, 0px, 0);
}

.menu-toggle.open .menu-line.mid {
    -webkit-transform: scale3d(0, 0, 0);
            transform: scale3d(0, 0, 0);
    opacity: 0;
}

.menu-toggle.open .menu-line.end {
    -webkit-transform: rotate(-45deg) translate3d(0, 0px, 0);
            transform: rotate(-45deg) translate3d(0, 0px, 0);
}


